:root {
  --black: #111;
  --theme: #678;
  --background: #F1F3F5;
  --blue: #00BFED;
  --light-green: #00D7C5;
  --purple: #AD1DE6;
  --bg-black: #303E4B;
  --yellow: #E99523;
  --red: #D81C60;
  --font-size: 16px;
}

* {
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  -moz-user-select: -moz-none;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  color: #111;
  color: var(--black);
}

body::-webkit-scrollbar {
  width: 10px;
}

body::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
}

body::-webkit-scrollbar-thumb {
  background-color: #678;
  background-color: var(--theme);
  outline: 1px solid #111;
  outline: 1px solid var(--black);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.App {
    height: 100%;
}
header {
	width: 100%;
	height: 15vh;
	z-index: 10;
	top: 0;
	background: transparent;
}

#header-container {
	width: 87%;
	height: 100%;
	margin: 0 auto;
	display: flex;
	align-items: center;
	justify-content: space-between;
}

#header-right ul {
	list-style: none;
	margin-top: -15px;
}


#header-right li {
	display: inline-block;
	margin-left: 0.7vw;
	font-weight: 500;
	color: var(--black);
}

.login {
	background: -webkit-gradient(linear, left top, left bottom, from(#8eb7f5), to(#40e1dc));
	background: -webkit-linear-gradient(#8eb7f5, #40e1dc);
	background: linear-gradient(#8eb7f5, #40e1dc);
	border-radius: 5vw;
	color: white;
	box-shadow: 2px 8px 25px rgba(0, 0, 0, 0.16);
	font-weight: 600;
	-webkit-transition: all 0.2s;
	transition: all 0.2s;
}

.login:hover {
	box-shadow: 2px 8px 25px rgba(0, 0, 0, 0.25);
}

.profile {
	height: 4vw;
	vertical-align: middle;
	margin-top: -10px;
}

#header-right a:last-child {
	text-decoration: none;
	color: inherit;
	display: block;
	padding: 1vw 2vw;
}


footer {
	width: 100%;
	clear: both;
	height: 25vh;
	position: relative;
	z-index: 10;
	background: url(/a2z-frontend/static/media/footer.7f435832.svg);
	background-repeat: no-repeat;
	background-size: cover;
}


#footer-container {
	width: 75%;
	margin: 0 auto;
	height: 100%;
	box-sizing: border-box !important;
	padding-top: 7vh;
	position: relative;
}

#footer-links-container {
	position: absolute;
	right: 0;
	margin-top: -20px;
}

.footer-link {
	display: flex;
	justify-content: center;
	align-items: center;
	box-shadow: 2px 8px 18px rgba(0, 0, 0, 0.16);
	margin: 7px;
	height: 2.3vw;
	width: 7vw;
	background: var(--black);
	border-radius: 4px;
	text-decoration: none;
	color: white;
	font-size: 1.5em;
	font-weight: bold;
	letter-spacing: 1px;
}

.footer-link img {
	width: 60%;
	margin-right: 5px;
}


#claim {
	text-align: center;
	opacity: 0.7;
	position: absolute;
	bottom: 2vh;
	left: 40%;
}

#claim p {
	font-size: 1.2em;
}

.side-links-container {
	position: fixed;
	top: 45%;
	left: 0px;
}


.side-link {
	-webkit-transform: translateX(-40px);
	transform: translateX(-40px);
	margin-top: 25px;
	background: var(--black);
	box-shadow: 2px 8px 30px rgba(0, 0, 0, 0.16);
	-webkit-transition: -webkit-transform 0.2s;
	transition: -webkit-transform 0.2s;
	transition: transform 0.2s;
	transition: transform 0.2s, -webkit-transform 0.2s;
}

.side-link:hover {
	-webkit-transform: translateX(0);
	transform: translateX(0);
}

.side-link a {
	display: block;
	padding: 25px 30px 25px 70px;
}

.side-link img {
	width: 32px;
}

@media only screen and (min-width: 2000px) {

	#header-right li {
		font-size: 2rem;
	}

	.footer-link img {
		width: 70%;
		margin-right: 20px;
	}

	.side-links-container {
		-webkit-transform: scale(0.4) translateX(-90px);
		transform: scale(0.4) translateX(-90px);
		top: 25%;
	}
}

@media only screen and (min-width: 1024px) and (max-width: 1940px) {

	#footer-links-container {
		margin-top: -10px;
	}

	.footer-link {
		border-radius: 4px;
		font-size: 0.8em;
		letter-spacing: 0px;
		font-size: 0.6em;
	}

	.footer-link img {
		width: 50%;
		margin-right: 0;
	}

	#claim p {
		font-size: 0.7em;
	}

	.side-links-container {
