.tag {
    padding: 4px 7px;
    border: 1px solid transparent;
    box-sizing: border-box;
    color: white;
    font-family: sans-serif;
    font-weight: 500;
    font-size: 0.6em;
    margin: 0.25em;
    border-radius: 2px;
    outline: none;
    display: inline-block;
    transition: all 0.2s;
}